:root {
    --white-color: #fff;
}
body {
    margin: 0;
    font-family: 'Josefin Sans', sans-serif;
    /* font-family: 'Open Sans', sans-serif; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 17px;
    padding: 0;
    box-sizing: border-box;
    background-color: #f5f5f5;
}
.col-lg-3 {
    padding: 0 4px;
}
.col-md-6 {
    padding: 0 4px;
}
.col-sm-12 {
    padding: 0 4px;
}
a {
    text-decoration: none;
    color: black;
}
a:hover {
    color: black;
}
i,
a,
p,
h1,
h2,
h3,
h4,
h5,
h6,
span {
    padding: 0;
    margin: 0;
}
input:focus,
select:focus,
textarea:focus {
    outline: 0px;
}
.row,
.col-6,
.col-4 {
    margin: 0;
    padding: 0;
}
.Announcement {
    background-color: #ee2624;
    padding: 15px 0;
}
.Announcement .container {
    padding-left: 0;
    padding-right: 0;
}
.Announcement .col-md-6 p {
    color: white;
    margin-right: 6rem;
    font-size: 15px;
}
.Announcement .col-12 a {
    color: white;
    /* margin-right: 2rem; */
    padding: 0 15px;
    font-size: 15px;
}
.Announcement .col-12 a:first-child {
    padding-left: 0;
}
.Announcement .col-12 a:last-child {
    padding-right: 0;
}
/* Header */
.header {
    padding: 10px 0px 10px 0px;
    border-bottom: 1.2px solid #ccc;
}
.navbar-brand img {
    width: 60%;
}
.input-group {
    height: 45px;
    width: 90%;
}
.search-button {
    width: 20%;
    background-color: #fb5533;
    color: white;
    border: 0;
    text-transform: uppercase;
    font-size: 12px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    width: 65px;
}
.submit-search {
    font-size: 20px;
}
.Login-Register a {
    text-transform: uppercase;
    font-size: 15px;
    margin-right: 2rem;
    position: relative;
}
.Login-Register a:nth-last-child(1) {
    margin-right: 0rem;
}
.badge {
    background-color: red;
    position: absolute;
    top: -20px;
    border-radius: 100px;
    padding: 5px 7px;
    font-size: 12px;
}
.name-button {
    padding: 5px 15px;
    border: 1px solid #e4e4e4;
    background-color: #f5f5f5;
    border-radius: 2px;
    margin: 0 15px;
}
.name-button__user {
    border: none !important;
    display: flex;
    align-items: center;
}
.name-button__div {
    margin-right: 4px;
    height: 40px;
    width: 40px;
    background-color: #939ea3;
    line-height: 43px;
    border-radius: 50%;
    border: 1.5px solid #c47e3c;
}
.name-button__span {
    color: white;
    font-size: 18px;
}
.dropdown-menu {
    background-color: black !important;
    border: 0 !important;
    padding: 0 !important;
    overflow: hidden;
}
.dropdown-menu a {
    padding: 10px 20px !important;
    color: #ffffff !important;
    margin: 0 !important;
}
.dropdown-menu a:hover {
    background-color: #1cb803 !important;
    color: #ffffff !important;
}
.mobile-header {
    display: none;
}
/* .mobile-header .container{
  display: flex;
} */
/* SHOP */
.section {
    width: 100%;
    padding-bottom: 6%;
    padding-top: 2%;
}
.sidebar_item {
    margin-bottom: 35px;
}
.sidebar_item h4 {
    color: #1c1c1c;
    font-weight: bold;
    font-size: 24px;
    margin-bottom: 25px;
}
.sidebar_item ul {
    margin: 0;
    padding: 0;
}
.sidebar_item ul li {
    list-style: none;
    display: flex;
}

.sidebar_item ul li a {
    font-size: 16px;
    color: #000000;
    line-height: 45px;
    display: block;
    font-family: 'Open Sans', sans-serif;
}
.sidebar_item ul li img {
    width: 25px;
    object-fit: contain;
    margin-left: 20px;
}
.shop {
    margin-bottom: 20px;
}
.border-product {
    /* padding: 0px; */
    /* border: 1.7px solid #ccc; */
    /* border-radius: 6px;
    box-shadow: 0 1px 6px rgb(0 0 0 / 12%); */
}
.border-product:hover {
    transform: translateY(-4px);
    transition: linear 0.2s;
}
.shopBack {
    height: 250px;
    position: relative;
    overflow: hidden;
    width: 100%;
    background-color: #fff;
    /* border-top-left-radius: 6px;
    border-top-right-radius: 6px; */
}
.shopBack img {
    width: 100%;
    height: 250px;
    object-fit: contain;
    filter: brightness(1);
    -webkit-filter: brightness(1);
}
.shoptext {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 5px;
    padding-right: 5px;
    background-color: #fff;
}
.shoptext p {
    line-height: 1.2;
    height: 38px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
}
.shoptext p a {
    color: #252525;
}
.shoptext h3 {
    color: #252525;
    margin-top: 5px;
    font-weight: bold;
    font-size: 19px;
}
.rating {
    margin: 5px 0;
}
.rating i {
    font-size: 12px;
    color: #ebb450;
}
.rating span {
    margin-left: 10px;
}
/* END OF SHOP */

/* NEWSLATTER */
.subscribe-section {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.226), rgba(0, 0, 0, 0.226)),
        url('https://bizweb.dktcdn.net/100/372/651/themes/802187/assets/slider_1.jpg?1638761605779') no-repeat scroll
            center / cover;
    padding: 100px 0 100px;
}
.subscribe-head h2 {
    color: #fff;
    font-size: 30px;
    letter-spacing: 0;
    line-height: 30px;
    margin: 0 0 13px;
    text-transform: uppercase;
}
.subscribe-head p {
    color: #fff;
    font-size: 18px;
    line-height: 22px;
    margin: 0 0 39px;
}
.subscribe-head {
    text-align: center;
}
.form-section input[type='email'] {
    border: 0 none;
    border-radius: 25px;
    color: #9b9b9b;
    font-size: 16px;
    font-style: normal;
    height: 51px;
    letter-spacing: 0;
    text-align: center;
    width: 460px;
}
.form-section input[type='submit'] {
    background: #ee2624;
    border: 0 none;
    border-radius: 25px;
    color: #fff;
    height: 52px;
    letter-spacing: 0;
    margin-left: -60px;
    padding: 0 58px;
    text-transform: uppercase;
    font-size: 12px;
}
/* CONTACT */
.contactInfo {
    padding: 70px 0;
}
.contact-Box,
.box-info,
.info-image {
    display: flex;
    justify-content: center;
    align-items: center;
}
.box-info {
    width: 80%;
    flex-direction: column;
    padding: 40px 0;
    box-shadow: 0px 0px 27px 0px rgb(243, 243, 243);
}
.box-info p {
    text-align: center;
    font-size: 17px;
}
.info-image {
    width: 50px;
    height: 50px;
    border: 2px solid #1cb803;
    border-radius: 100px;
    font-size: 20px;
    color: #1cb803;
}
.box-info h5 {
    font-weight: bold;
    font-size: 19px;
    margin-top: 20px;
    margin-bottom: 4px;
}
.box-info p {
    font-size: 16px;
}

/* SINGLE PRODUCT */
.product-postion {
    position: relative;
}
.single-product {
    margin-top: 50px;
    margin-bottom: 50px;
}
.single-image {
    background-color: #fafafa;
    margin-right: 20px;
    height: 550px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1.5px solid #cccc;
    border-radius: 8px;
    box-shadow: 0 1px 6px rgb(0 0 0 / 12%); */
}
.single-image img {
    width: 100%;
    height: 550px;
    object-fit: scale-down;
    filter: brightness(0.98);
    -webkit-filter: brightness(0.98);
}
.product-name {
    font-size: 25px;
    font-weight: 600;
    line-height: 1.2;
    margin-bottom: 10px;
}

.display-flex {
    display: flex;
}
.product-info {
    width: 100%;
}
.product-dtl p {
    line-height: 24px;
    color: #7a7a7a;
}
.product-count {
    /* border: 2px solid #f3f3f3; */
    border-radius: 5px;
    margin-top: 10px;
}
.product-count .flex-box {
    padding: 12px 25px;
    /* border-bottom: 2px solid #f3f3f3; */
}
.product-description {
    padding-top: 20px;
    margin-top: 10px;
    background-color: #fff;
    border-radius: 4px;
}
.product-description h2 {
    width: 150px;
    border-bottom: 2px solid #cccc;
}
.product-description p {
    padding: 20px 0;
}
.product-count .flex-box h6 {
    font-size: 16px;
}
.product-count .flex-box span {
    font-weight: 600;
    color: #000000;
}
.product-count .flex-box select {
    background-color: #f3f3f3;
    width: 100px;
    height: 40px;
    text-align: center;
    border: 0px;
    border-radius: 5px;
}
.round-black-btn {
    border-radius: 4px;
    background: #ff5722;
    bottom: 0;
    color: #fff;
    width: 100%;
    height: 50px;
    border: 0;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 15px;
    transition: all 0.5s ease-in-out 0s;
}
.round-black-btn:hover,
.round-black-btn:focus {
    background: #1cb803;
}
.bg-orange {
    background: #ff5722;
}
.rating-review {
    height: 50vh;
    overflow-y: scroll;
}
.rating-review::-webkit-scrollbar {
    width: 3px;
}
.rating-review::-webkit-scrollbar-thumb {
    background: #ff5722;
    border-radius: 8px;
}
/* .rating-review__flex {
    display: flex;
    align-items: center;
} */
.rating-review__flex p {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color: #f5f5f5;
    text-align: center;
    line-height: 40px;
    font-size: 18px;
    font-weight: 600;
    border: 1px solid #ff5722;
}
.alert-info {
    background-color: #f0eeeb;
    border: none;
}
.review-rating .rating {
    padding-left: 5px;
}
.product-avatar {
    background-color: #fff;
    padding: 15px 0;
    border-radius: 4px;
}
.product-rating {
    background-color: #fff;
    margin-top: 10px;
    border-radius: 4px;
}

.btn__product-option {
    line-height: '1.5rem';
    padding: '0.2rem 0.5rem 0.15rem 0.5rem';
    min-width: '22.5rem';
    margin: 5px 5px 5px 0px;
    border-radius: 0;
    font-size: 1rem;
    border: 1px solid rgb(163, 155, 155);
    color: rgb(123, 119, 119);
    margin-right: 5px;
}

.btn__product-option + .btn__product-option {
    margin-right: 5px;
}
.btn__product-option:hover {
    border: 1px solid #ee4d2d;
    color: #ee4d2d;
}

.btn__product-option.active {
    border: 1px solid #ee4d2d;
    position: relative;
    color: #ee4d2d;
    outline: none;
}
.btn__product-option.active::before {
    outline: none;
    border: 1rem solid transparent;
    border-bottom: 0rem solid var(--brand-primary-color, #ee4d2d);
    border-right: 1.5rem solid var(--brand-primary-color, #ee4d2d);
    content: '';
    right: 0rem;
    position: absolute;

    bottom: -0.1999999999999993px;
}

.input-quantity {
    width: 50px;
    height: 32px;
    border-left: 0;
    border-right: 0;
    font-size: 16px;
    font-weight: 400;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    text-align: center;
    cursor: text;
    border-radius: 0;
    -webkit-appearance: none;
    outline: none;
    cursor: pointer;
    border: 0;
    /* line-height: 1; */
    letter-spacing: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.1s cubic-bezier(0.4, 0, 0.6, 1);
    border: 1px solid rgba(0, 0, 0, 0.09);
    border-radius: 2px;
    background: transparent;
    color: rgba(0, 0, 0, 0.8);
}
.input-quantity.icon {
    width: 32px;
    height: 32px;
    font-size: 0.875rem;
    font-weight: 300;
}

.remove-arrow-input::-webkit-outer-spin-button,
.remove-arrow-input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
