.stepper__line {
    position: relative;
    top: 0px;
    height: 4px;
    width: 50px;
    color: rgb(239, 220, 220);
    background-color: rgb(239, 210, 210);
}

.stepper__line-background,
.stepper__line-foreground {
    position: absolute;
    width: calc(100% - 140px);
    margin: 0 70px;
    height: 100%;
    box-sizing: border-box;
    background: rgb(215, 11, 11);
}

.stepper__line-foreground {
    width: calc((100% - 140px) * 1);
    background: rgb(45, 194, 88);
    background: #2dc258;
}

.order-arrow-status + .order-arrow-status {
    margin-right: 200px;
    margin-left: 200px;
}

.order-status-history-line {
    position: absolute;
    height: 5px;
    width: 100%;
    top: 50%;
    bottom: 10px;
    right: -50%;
    background-color: rgba(0, 0, 0, 0.09);
}

/* search */
.search-wrap {
    position: relative;
    width: 100%;
}

.inner {
    height: 100px;
    width: 100%;
}
.search-item {
    min-height: 55px;

    /* text-align: center; */
}
.search-item:hover {
    background-color: rgba(0, 0, 0, 0.09);
}
/* .search-item:first-child {
    margin-top: 20px;
} */
.search-item span {
    display: block;
    line-height: 1rem;
    overflow: hidden;
    white-space: nowrap;
    padding-left: 5px;
    text-overflow: ellipsis;
}

.search-btn-header:focus {
    border: none !important;
}

.product-card + .product-card {
    margin-left: 15px;
}
.product-card :hover img {
    transform: translateY(-3px);

    transition: all 0.2s ease;
}

.corousel-price {
    padding: 0;
    padding-left: 0 !important;
}
.corousel-noti {
    padding: 0 !important;
    text-align: start;
}
.corousel-rating {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

